    position: relative;

width: 33%;
background-color: blueviolet; 
}
.MainPosts .likes{
    position: absolute;
    top: 5px;
    right: 5px;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: end;
            justify-content: end;
    -webkit-align-items: center;
            align-items: center;

    height: 30px;
    width: auto;

cursor: pointer; /* wherever user click like is added user click */  
}
.MainPosts .likes p , 
.MainPosts .likes i{
color: white;
font-size: 1.3rem;

margin: 0 0.2rem;

pointer-events: none;
}


.MainPosts .link{ 
color: white;


    font-size: 2rem;
    text-decoration: none;
    position: relative; 
}
.MainPosts .link::after {
    position: absolute;
    content: '';
    height: 2px;
    bottom: 5px; 

    margin: 0 auto;
      left: 0;
    right: 0;
      width: 100%;
      background: white;
    transition:.5s;
}

 .MainPosts .link:hover::after {
    width: 50%;
    background: white;    
}


.MainPosts .tag {
    font-size: 1.2rem;

  text-align: center;
  width: 80px;
  height: auto;

  transition: all 500ms ease;
  border: 2px solid transparent;
}
.MainPosts .tag:hover{
    opacity: 0.9;
    cursor: pointer;
    border-color: white;
}

